import React, { useState, useRef } from 'react'
import styled from 'styled-components'
import Modal from 'react-awesome-modal';
import SignatureCanvas from 'react-signature-canvas'
import {FaPenNib} from 'react-icons/fa'
import { ColorRgb, textColor,InputStyle } from '../../theme'

const today = new Date(); const month = (today.getMonth()+1); const day = today.getDate();
const Currentdate = today.getFullYear() + '-' + (month.toString().length ===1?'0'+month : month) + '-' + (day.toString().length ===1?'0'+day : day);

function PageOne(props) {
      const ref = useRef();
      const [ why, setWhy ] = useState(false);
      const [visible, setVisible] = useState(false);
      let [sigCanvas, setSigCanvas] = useState({});
      let [trimmedDataURL, setTrimmedDataURL] = useState(null);
      const openModal=()=> { setVisible(true); }
      const closeModal=()=> { setVisible(false);}
      const clear = () => sigCanvas.clear();
      const trim = () =>{ setTrimmedDataURL(sigCanvas.getTrimmedCanvas().toDataURL('image/png')); props.setImgData(sigCanvas.getTrimmedCanvas().toDataURL('image/png')); closeModal();};
      const changeHandle = (event) =>{ if(event.target.value==="true"){ setWhy(true); ref.current.focus(); }else{ setWhy(false); } }

      return (
        <>
            <MainPar className="MainPar">
                    <div className="title">     </div>
                    <div className="nameTitle">  : <span>{props.userName}</span></div>
                    <div className="MemeberInfo"><span className="titleSm"> : </span> 
                        <InputStyle className="inpp">
                        {
                          props.propData?
                          <input className="getInputt" name="occupation" value={props.propData.occupation} type="text" placeholder="   ..." />  : 
                          <input className="getInputt" name="occupation" type="text" placeholder="   ..." /> 
                        } 
                         <div className="line"></div></InputStyle>
                    </div>
                    <div className="contentPar">
                        <div className="items">     ,           . </div>
                        <div className="items">    : </div>
                        <div className="Content">
                            <div className="Titles">I-      </div>
                            <ul>
                                <li className="items">
                                                            . <br /><br />
                                    [     ,                .]<br />
                                    <div className="ZorchilPar"><span className="titleBig">        :</span> 
                                    { props.propData?
                                     <div className="childPar">
                                          <div className="child"> <input className="radio getInputt" checked={props.propData.is_violation?true:false} id="radio" name="is_violation" value="true" type="radio" /><span className="smTitle"></span></div>
                                          <div className="child childA"><input className="radio getInputt" checked={props.propData.is_violation?false:true} name="is_violation" value="false" type="radio" /><span className="smTitle"></span></div>
                                      </div>
                                      :
                                      <div className="childPar">
                                          <div className="child"> <input className="radio getInputt" onChange={changeHandle} id="radio" name="is_violation" value="true" type="radio" /><span className="smTitle"></span></div>
                                          <div className="child childA"><input className="radio getInputt" onChange={changeHandle} name="is_violation" value="false" type="radio" /><span className="smTitle"></span></div>
                                      </div>
                                    }
                                      
                                      
                                    </div>  
                                </li>
                                {props.propData? props.propData.is_violation? 
                                <InputStyle >
                                  <textarea ref={ref} id="reason" name="uussen_zorchil" value={props.propData.uussen_zorchil} placeholder="      ..." />
                                  <div className="line"></div>
                                </InputStyle>
                                 : 
                                  <InputStyle style={why?{transform:`scale(1)`}:{transform:`scale(0)`}}>
                                  <textarea ref={ref} id="reason" name="uussen_zorchil" placeholder="      ..." />
                                  <div className="line"></div>
                                </InputStyle> 
                                 :
                                <InputStyle style={why?{transform:`scale(1)`}:{transform:`scale(0)`}}>
                                  <textarea ref={ref} id="reason" name="uussen_zorchil" placeholder="      ..." />
                                  <div className="line"></div>
                                </InputStyle> }
                                
                                <li className="items"> 
                                                         .           . 
                                </li>
                                <div className="Titles">II-      </div>
                                <li className="items">           , ,   ,             . </li>
                                    <ul className="smUl">
                                            <li>   	                 , ,  ,   . </li>
                                            <li>"  "        ,    ,       ,   ; </li>
                                            <li>"  "                 ; </li>
                                            <li>"  "                       </li>
                                            <li>   ,  ,              ,  , ,            , ,     </li>
                                    </ul>
                                <li className="items">    ,        .</li>
                                <li className="items">   ,   .</li>
                                <li className="items">              , ,    .</li>
                                <li className="items">          1  2-          .</li>
                                <li className="items">       .</li>
                                <li className="items">    ,      ,   </li>
                                <li className="items">             .     </li>
                                <li className="items">         ,         ,      . </li>
                                <li className="items">          .</li>
                                <li className="items">          ,  .</li>
                            </ul>
                        </div>
                    </div>

                    <div className="signature">
                        {/* <div className="title">  : <div className="signatureItem">/........................................./ </div>  </div> */}
                        <div className="drowPar">
                              <div className="titleee"> :</div>
                              {!props.propData&&<div className="SignBtn" onClick={()=>openModal()} ><FaPenNib /><span></span></div>} 
                              {props.propData? <img className="SingatureImg"  src={props.propData.signature_data}/>  : trimmedDataURL ? <img className="SingatureImg"  src={trimmedDataURL}/> : null }
                              {/* signature_data */}
                                    <Modal visible={visible}  width="620" height="380"effect="fadeInDown" onClickAway={closeModal}>
                                        <div className="modalPar">
                                            <div className="Canvass">
                                                <SignatureCanvas className='sigCanvas' penColor='blue' ref={(ref) => { sigCanvas = ref }} canvasProps={{width: 620, height: 310, className: 'sigCanvas'}} />
                                            </div>
                                            <div className="BtnPar">
                                                <button onClick={clear}></button>
                                                <button onClick={()=>trim()}></button>
                                                <button onClick={closeModal}>X</button>
                                            </div>
                                        </div>
                                    </Modal>
                        </div>
                    </div>

                    <div className="datePar">
                        <span className="Title">: </span>
                        <InputStyle>
                            { props.propData? <input className="getInputt" value={props.propData.sdate} name="sdate" max={Currentdate} type="date" /> : <input className="getInputt" name="sdate" max={Currentdate} type="date" /> }
                          <div className="line"></div>
                        </InputStyle>
                    </div>

             </MainPar>
             </>
        )
  }
  export default PageOne

const MainPar = styled.div`
    margin-bottom:30px;
    background-color:white;
    // max-width:700px;
    margin-top:20px;
    font-size:13px;
    padding:25px 100px;
    border:1px solid rgba(0,0,0,.3);
    color:rgba(${textColor},1);
    .datePar{
      padding-bottom:30px;
      display:flex;
      align-items:center;
      .Title{
        font-weight:500;
        margin-right:20px;
        font-size:14px;
      }
    }
    .signature{
      padding:30px 0px;
          .drowPar{
            display:flex;
            align-items:start;
            margin-top:10px;
            .titleee{
              font-weight:500;
              margin-right:30px;
              margin-bottom:15px;
            }
            .SignBtn{
                // margin-left:30px;
                margin-right:30px;
                padding:3px 15px;
                cursor:pointer;
                display:flex;
                align-items:center;
                border:1px solid rgba(0,0,0,0.4);
                svg{
                    margin-right:10px;
                }
                span{
                  // font-weight:500;
                }
                &:hover{
                    background-color:rgba(0,0,0,.2);
                }
            }
            
            .SingatureImg{
                border:1px solid rgba(${ColorRgb},0.3);
                width:200px;
                height:100px;
                object-fit:contain;
          }

            .modalPar{
                text-align:center;
            .Canvass{
                border:1px solid rgba(${ColorRgb},0.5);
            }
                .BtnPar{
                padding:0px 10px;
                margin:20px 0px;
                display:flex;
                flex-direction:row;
                align-items:center;
                justify-content:space-between;
                button{
                    font-weight:500;
                    color:rgba(${textColor},0.9);
                    cursor:pointer;
                    border-style:none;
                    border-radius:4px;
                    padding:6px 14px;
                    background-color:white;
                    box-shadow:1px 1px 8px -2px;
                }
                }
            }
        }


    }
    .title{
      color:rgb(${textColor});
      font-weight:500;
      margin:10px 0px;
      margin-bottom:30px;
      text-align:center;
      font-size:16px;
    }
    .MemeberInfo{
      margin-top:8px;
      margin-bottom:25px;
      display:flex;
      align-items:center;
      .titleSm{
        width:20%;
        // font-weight:500;
        margin-right:15px;
      }
      .inpp{
        font-weight:400;
        width:80%;
      }
    }
    .nameTitle{
      // font-weight:500;
      span{
        margin-left:15px;
        font-weight:400;
      }
    }
    .contentPar{
      margin-top:15px;
      .items{
        margin:10px 0px; 
      }
      .Content{
        padding:0px 50px;
        .Titles{
          margin:25px 0px;
          text-align:center;
        }
        ul{
          list-style-type:decimal;
          .items{
            .ZorchilPar{
              margin:10px 0px;
              .titleBig{
                font-weight:500;
              }
              .childPar{
                display:flex;
                align-items:center;
                margin-top:10px;
                .child{
                  display:flex;
                  align-items:center;
                  .smTitle{
                    margin-left:5px;
                  }
                  .radio{
                    cursor:pointer;
                    width:16px;
                    height:16px;
                  }
                }
                .childA{
                  margin-left:30px;
                }
              }
            }
          }
          .form-control{
            font-size:0.9em;
          }
          .smUl{
            padding-left:50px;
            list-style-type:upper-roman;
          }
        }
        

      }
      
    }
    @media (max-width:768px){
      
      .signature{
        .drowPar{
          flex-direction:column;
          .SignBtn{
            margin:15px 0px;
          }
        }
      }
      .MemeberInfo{
        align-items:start;
        flex-direction:column;
        .titleSm{
          width:100%;
        }
      }
      padding: 15px 10px;
      .contentPar{
        .Content{
          padding: 0px 25px;
          ul{
            .smUl{
              padding-left: 35px;
            }
          }
        }
      }
     
    }

    @media print{
      margin-bottom:30px;
      background-color:white;
      max-width:1200px;
      margin-top:20px;
      font-size:15px;
      padding:15px 80px;
      border:none;
      .contentPar{
        padding: 30px 25px;
      }
    }
`

