import React from 'react'
import { Link } from "react-router-dom";
import DocumentTitle from 'containers/document/DocumentTitle';
import { edplanFront } from "axiosbase"

function ActiveComp(props) {
    DocumentTitle("EDP");

    return (
        <div style={{ paddingTop: 20 }} className="row">
            <div className="col-md-2 col-sm-2 itemsCol">
                <div className="itemsPar">
                    <div className="mains">
                        <Link to={!props.prew ? `/check/user` : `/check/${props.prew}`} className={props?.data?.criteria !== 0 ? props?.data?.criteria === 2 ? `itemsActive` : `items items` : `items`}>1.    </Link>
                        {/* <div className="line line2" ></div> */}
                        <div className="line" />
                        <Link to={!props.prew ? `/request/user` : `/request/${props.prew}`}
                            className={!props?.data?.esm ? `items` : props?.data?.esm === "A" ? `items itemsNotApproved` : props?.data?.esm === "B" ? `itemsActive` : props?.data?.esm === "C" || props?.data?.esm === "F" ? `itemsActive` : `items itemsNotWait`}>2.     </Link>
                        <div className="line" />
                        <Link to={props.prew ? `/letter-of-interest?userId=${props.prew}` : '/letter-of-interest'} className={props?.data?.letterOfInterst === true ? `itemsActive` : `items`} >3.    </Link>
                        <div className="line" />
                        <Link to={props.prew ? `/urgudul-preview/${props?.data?.project?.id}` : '/urgudul/1'} className={props?.data?.project && props?.data?.project.confirmed === 1 ? `itemsActive` : `items`} >4.   </Link>
                        <div className="line" />
                        <Link to={props.prew ? `/attachments/evidence-1/${props.prew}` : '/attachments/evidence-1'} className={props?.data?.evidence1 === true ? `itemsActive` : `items`} >5.    I</Link>
                    </div>
                    {/* <div className="lineFull" ></div>
                    <Link to={props.prew ? `/urgudul-preview/${props?.data?.project?.id}` : '/urgudul/1'} className={props?.data?.project && props?.data?.project.confirmed === 1 ? `resultActive` : `resultDesable`} >4.   </Link> */}
                </div>
            </div>

            <div className="col-md-2 col-sm-2 itemsCol">
                <div className="itemsPar">
                    <div className="mains">
                        <a href={props.prew ? `` : `${edplanFront}/${props?.data?.userId}`} rel="noreferrer" target={props.prew ? "_self" : "_blank"} className={props?.data?.edpPlan === true ? `itemsActive arrHelp arrHelpActive` : `items arrHelp`}><div className="helpArr"></div> 1.  </a>
                        <div className="line" ></div>
                        <Link to={props.prew ? `/attachments/evidence-2/${props.prew}` : '/attachments/evidence-2'} className={props?.data?.evidence2 === true ? `itemsActive` : `items`}>
                            2.   II
                        </Link>
                    </div>
                    {/* <div className="lineFull" ></div> */}
                    {/* <Link to={props.prew ? `/attachments/${props.prew}` : '/attachments'} className={props?.data?.evidence === true ? `resultActive` : `resultDesable`} >2.   </Link> */}
                </div>
            </div>

            <div className="col-md-2 col-sm-2 itemsCol">
                <div className="itemsPar">
                    <div className="mains">
                        <Link to={props.prew ? `/5a/${props?.data?.project?.id}` : ''} className={props?.data?.firstEvalution.value === 0 ? `items arrHelp` : props?.data?.firstEvalution.value === 1 ? `items itemsNotApproved arrHelp` : `itemsActive arrHelp`}>
                            <div className="helpArr" />
                            1.   
                        </Link>
                        <div className="line" />
                        <Link to={props.prew ? `/5b/${props?.data?.project?.id}` : ''} className={props?.data?.evaluation5b.value === 0 ? 'items' : props?.data?.evaluation5b.value === 1 ? 'itemsNotApproved items' : 'itemsActive items'}>
                            <div className="helpArr"></div>
                            2.     
                        </Link>
                        <div className="line" />
                        <Link to={props.prew ? `/5c/${props?.data?.project?.id}` : ''} className={props?.data?.evaluation5c.value === 0 ? 'items' : props?.data?.evaluation5c.value === 1 ? 'itemsNotApproved items' : 'itemsActive items'}>
                            3.  
                        </Link>
                    </div>
                    {/* <div className="lineFull" ></div>
                    <Link to={props.prew ? `/5c/${props?.data?.project?.id}` : '/5c'} className={props?.data?.evaluation5c ? `items resultActive` : `items resultDesable`}>3.     </Link> */}
                </div>
            </div>

            <div className="col-md-2 col-sm-2 itemsCol">
                <div className="itemsPar">
                    <div className="mains">
                        <Link to="" className={props?.data?.lastEvalution.value === 0 ? `items arrHelp` : props?.data?.lastEvalution.value === 1 ? `itemsActive` : `itemsActive`} ><div className="helpArr"></div> 1.     </Link>
                        <div className="line" />
                        <Link to={props.prew ? `/maindecision/${props?.data?.project?.id}` : ``} className={props?.data?.lastEvalution.value === 0 ? `items` : props?.data?.lastEvalution.value === 1 ? `resultWaiting` : `itemsActive`} >2.   </Link>
                    </div>
                    {/* <div className="lineFull" ></div>
                    <Link to={props.prew ? `/maindecision/${props?.data?.project?.id}` : ``} className={props?.data?.lastEvalution.value === 0 ? `resultDesable` : props?.data?.lastEvalution.value === 1 ? `resultWaiting` : `resultActive`} >2.   </Link> */}
                </div>
            </div>

            <div className="col-md-2 col-sm-2 itemsCol">
                <div className="itemsPar">
                    <div className="mains">
                        <Link to="/make-contract">
                            <div className="items arrHelp"><div className="helpArr"></div>1.   </div>
                        </Link>
                        <div className="line"></div>
                        <div className="items">2.    </div>
                        <div className="line"></div>
                        <Link to={props.prew ? `/attachments/performance/${props.prew}` : '/attachments/performance'}>
                            <div className="items">3.    (,  ,   )</div>
                        </Link>
                    </div>
                    {/* <div className="lineFull" ></div>
                    <div className="resultDesable">3.    (,  ,   )</div> */}
                </div>
            </div>

            <div style={{ borderRight: `none` }} className="col-md-2 col-sm-2 itemsCol itemsColA">
                <div className="itemsPar">
                    <div className="mains">
                        <Link to={props.prew ? `/attachments/financial/${props.prew}` : '/attachments/financial'}>
                            <div className="items arrHelp"><div className="helpArr"></div>4.    </div>
                        </Link>
                        <div className="line" ></div>
                        <Link to={props.prew ? `/attachments/invoice/${props.prew}` : '/attachments/invoice'}>
                            <div className="items">5.    </div>
                        </Link>
                        <div className="line"></div>
                        <Link to="/work-performance">
                            <div className="items">6.     </div>
                        </Link>
                        <div className="line"></div>
                        <Link to="/control-report">
                            <div className="items">7.      </div>
                        </Link>
                        <div className="line"></div>
                        <div className="items">8.   ( ,  , )</div>
                    </div>
                    {/* <div className="lineFull" ></div>
                    <div className="resultDesable">6.   ( ,  , )</div> */}
                </div>
            </div>
        </div>
    )
}

export default ActiveComp
